* {
  margin: 0px;
  padding: 0px;
}
@import url("https://fonts.googleapis.com/css2?family=Trispace:wght@300&display=swap");
/* Initial Animations */
main {
  width: 100vw;
  height: 100vh;
}
.enter {
  position: relative;
  opacity: 0;
  width: 12em;
  height: 6em;
  background-color: transparent;
  border-style: groove;
  border-color: white;
  left: 50%;
  top: -10%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  animation: button 6s forwards 4s;
}
@keyframes button {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    pointer-events: all;
  }
}
.background {
  height: 100vh;
  width: 100vw;
}
.title {
  position: relative;
  height: 600px;
}
.ultimate {
  opacity: 0;
  position: absolute;
  top: 0%;
  animation: ult 3s forwards 1s;
}

@keyframes ult {
  0% {
    opacity: 0.1;
  }

  100% {
    opacity: 1;
    top: 0px;
  }
}
.pack {
  opacity: 0;
  position: absolute;
  top: 0px;
  animation: pac 4s forwards 1.5s;
}
@keyframes pac {
  0% {
    opacity: 0.1;
    top: 0px;
  }

  100% {
    opacity: 1;
    top: 200px;
  }
}
.opening {
  opacity: 0;
  position: absolute;
  top: 0%;
  animation: ope 4s forwards 2s;
}
@keyframes ope {
  0% {
    opacity: 0.1;
    top: 0px;
  }

  100% {
    opacity: 1;
    top: 400px;
  }
}
.border {
  position: relative;
  color: white;
  font-size: 150px;
  text-shadow: 1px black;
  height: 800px;
  width: 100vw;
  user-select: none;
}

body {
  position: relative;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(
    90deg,
    rgb(13, 177, 214),
    rgb(194, 10, 194),
    rgb(165, 10, 194)
  );
  background-size: 400% 400%;
  animation: cool 15s ease infinite;
}
@keyframes cool {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 50% 100%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Pack Opening  */
.deck {
  position: absolute;
  top: 25%;
  width: 100vw;
  height: 75vh;
}

.Packs {
  position: absolute;
  width: 100vw;
  left: 50%;
  align-content: center;
  transform: translate(-50%, 0);
}
.Packs * {
  margin-left: 250px;
}

.card {
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  align-content: center;
  color: black;
  height: 20em;
  width: 12.5em;
  transform: translate(-50%, 0);
}

.bronzePack {
  opacity: 0;
  pointer-events: none;
  background-color: #cd7f32;
  height: 20em;
  width: 12.5em;
  border-style: none;
}
.silverPack {
  opacity: 0;
  pointer-events: none;
  background-color: #c0c0c0;
  height: 20em;
  width: 12.5em;
  border-style: none;
}
.goldPack {
  opacity: 0;
  pointer-events: none;
  background-color: #ffd700;
  height: 20em;
  width: 12.5em;
  border-style: none;
}
